import React, { PureComponent } from 'react';
import classnames from 'classnames';
import { styled } from 'linaria/react';
import throttle from 'lodash.throttle';
const SHIFT_SPEED = 0.7;
const SHIFT_AMOUNT = 150;
const Root = /*#__PURE__*/styled("div")({
  name: "Root",
  class: "r1iy7xvg"
});
const Wrapper = /*#__PURE__*/styled("div")({
  name: "Wrapper",
  class: "w1ex1xtp"
});
const Content = /*#__PURE__*/styled("div")({
  name: "Content",
  class: "c1d9yujx"
});
const ArrowContainer = /*#__PURE__*/styled("div")({
  name: "ArrowContainer",
  class: "abva993"
});
const LeftArrowContainer = /*#__PURE__*/styled(ArrowContainer)({
  name: "LeftArrowContainer",
  class: "lerlrwy"
});
const RightArrowContainer = /*#__PURE__*/styled(ArrowContainer)({
  name: "RightArrowContainer",
  class: "r1vkdriz"
});

function nextAnimationFrame(callback: () => void) {
  if (window.requestAnimationFrame) {
    window.requestAnimationFrame(callback);
  } else {
    setTimeout(() => callback(), 16);
  }
}

export class SlideContainer extends PureComponent<React.HTMLAttributes<HTMLDivElement>> {
  state: {
    width: number;
    scrollLeft: number;
    contentWidth: number;
  } = {
    width: 0,
    scrollLeft: 0,
    contentWidth: 0
  };
  contentRef = React.createRef<HTMLDivElement>();
  wrapperRef = React.createRef<HTMLDivElement>();
  stepTs = 0;
  renderLazy = throttle(() => {
    this.setState({
      contentWidth: this.contentRef.current?.offsetWidth || 0,
      scrollLeft: this.wrapperRef.current?.scrollLeft || 0,
      width: this.wrapperRef.current?.scrollWidth || 0
    });
  }, 50, {
    leading: false
  });

  componentDidMount() {
    window.addEventListener('resize', this.renderLazy);
    this.renderLazy();
  }

  componentDidUpdate() {
    this.renderLazy();
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.renderLazy);
    this.renderLazy.cancel();
  }

  onLeftClick = () => {
    this.scroll(SHIFT_AMOUNT, true);
  };
  onRightClick = () => {
    this.scroll(SHIFT_AMOUNT);
  };
  scroll = (px: number, left?: boolean) => {
    this.stepTs = Date.now() - 16;
    this.step(px, left);
  };
  step = (px: number, left?: boolean) => {
    const now = Date.now();
    const delta = now - this.stepTs;
    this.stepTs = now;
    const shift = Math.round(delta * SHIFT_SPEED);

    if (this.wrapperRef.current) {
      if (left) {
        this.wrapperRef.current.scrollLeft -= Math.min(px, shift);
      } else {
        this.wrapperRef.current.scrollLeft += Math.min(px, shift);
      }
    }

    const remain = px - shift;

    if (remain > 0) {
      nextAnimationFrame(() => {
        this.step(remain, left);
      });
    }
  };

  render() {
    const {
      children,
      className
    } = this.props;
    const {
      contentWidth,
      scrollLeft,
      width
    } = this.state;
    let left = false;
    let right = false;

    if (this.contentRef.current) {
      left = scrollLeft !== 0;
      right = width > scrollLeft + contentWidth;
    }

    return <Root className={classnames(className, {
      leftshade: Boolean(left),
      rightshade: Boolean(right)
    })}>
        {left ? <LeftArrowContainer onClick={this.onLeftClick} /> : undefined}
        <Wrapper ref={this.wrapperRef} onScroll={this.renderLazy}>
          <Content ref={this.contentRef}>{children}</Content>
        </Wrapper>
        {right ? <RightArrowContainer onClick={this.onRightClick} /> : undefined}
      </Root>;
  }

}